<template>
    <div class="outbreakArea">
        <iframe
            class="ifr"
            frameborder="0"
            scrolling="auto"
            allow="geolocation"
            src="https://ncp-h5.newayz.com/?channel=xiaomi"/>
        <div
            v-if="!isXK"
            class="more"
            @click="toXiangkan"/>
        <div
            v-else
            class="share"
            @click="share"/>
        <gray-mask
            :show="showGray"
            @click="setGray(false)">
            <div class="share-icon"/>
        </gray-mask>
    </div>
</template>

<script>
import GrayMask from '@/components/GrayMask';
import { goAppStore, setShareInfo } from '@/utils/share';
import { isXiangkan } from '@/utils/ua';
import { shareWithSocialType } from '@/utils/jsApiV2/act';

export default {
    name: 'OutbreakArea',
    components: {
        GrayMask,
    },
    data() {
        return {
            showGray: false,
            isXK: isXiangkan(),
        };
    },
    computed: {
        trackEnv() {
            return this.isXK ? '' : '微信';
        },
    },
    created() {
        this.$root.hidePlaceholder();
        this.$o2o({
            e_c: `${this.trackEnv}周围小区疫情`,
            e_a: '曝光',
            e_n: `${this.trackEnv}小区疫情曝光`,
        });
        setShareInfo({
            title: '周边小区免疫速查-想看',
            summary: '实时新型冠状病毒肺炎疫情查询，众志成城，全民加油！',
            pic: 'http://t1.market.xiaomi.com/download/Browser/09be595f33b8d46b327899cb362b1b0b9698accc4',
            url: window.location.href,
        });
    },
    methods: {
        toXiangkan() {
            this.$o2o({
                e_c: '微信周围小区疫情',
                e_a: '点击',
                e_n: '下载',
            });
            goAppStore('bikan://web.tab/newsTab', 'CK1451326857236');
        },
        share() {
            this.$o2o({
                e_c: `${this.trackEnv}周围小区疫情`,
                e_a: '点击',
                e_n: '分享',
            });
            if (this.isXK) {
                shareWithSocialType({
                    title: '周边小区免疫速查-想看',
                    url: window.location.href,
                    text: '实时新型冠状病毒肺炎疫情查询，众志成城，全民加油！',
                    thumbUrl: 'http://t1.market.xiaomi.com/download/Browser/09be595f33b8d46b327899cb362b1b0b9698accc4',
                    socialType: 2,
                });
            } else {
                this.setGray(true);
            }
        },
        setGray(show) {
            this.showGray = show;
        },
    },
};
</script>

